import React, {Component} from 'react';
import PropTypes from 'prop-types';
import _ from "lodash";
import RGL, { WidthProvider } from "react-grid-layout";
import "../assets/dragdash.css"

const ReactGridLayout = WidthProvider(RGL);


/**
 * GridComponent is an dash component.
 * It takes a property, `id`, and children to
 * displays it.
 */
export default class DashGrid extends Component {

      constructor(props) {
        super(props);

        const layout = this.generateLayout();
        this.state = { layout };
      }

      generateDOM() {
        return _.map(_.range(this.props.items), function(i) {
          return (
            <div key={i}>
              <span className="text">{i}</span>
            </div>
          );
        });
      }

      generateLayout() {
        const p = this.props;
        return _.map(new Array(p.items), function(item, i) {
          const y = _.result(p, "y") || Math.ceil(Math.random() * 4) + 1;
          return {
            x: (i * 2) % 12,
            y: Math.floor(i / 6) * y,
            w: 2,
            h: y,
            i: i.toString()
          };
        });
      }

      onLayoutChange(layout) {
        this.props.onLayoutChange(layout);
      }

    render() {
        const {id, children, setProps } = this.props;
        console.log(children)
        return (
            <div id={id}>
                    <ReactGridLayout
                        layout={this.state.layout}
                        onLayoutChange={this.onLayoutChange}
                        {...this.props}
                      >
                      {this.props.children.map((child, index) => (<div key={index}>{child}</div>))}
                    </ReactGridLayout>
            </div>
        );
    }
}

DashGrid.defaultProps = {
    className: "layout",
    items: 50,
    cols: 12,
    rowHeight: 30,
    onLayoutChange: function() {},
    // This turns off compaction so you can place items wherever.
    verticalCompact: false,
    // This turns off rearrangement so items will not be pushed arround.
    preventCollision: true
};

DashGrid.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The children components displayed inside the grid.
     */
    children : PropTypes.node,
    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
